:root {
  --bg: #f6f1e8;
  --card: #fffdf8;
  --text: #1f1f1f;
  --muted: #666;
  --accent: #0a7a5c;
  --border: #d9d2c7;
}

* { box-sizing: border-box; }

body {
  margin: 0;
  font-family: "Avenir Next", "Gill Sans", "Trebuchet MS", sans-serif;
  background: radial-gradient(circle at top right, #efe7da 0%, var(--bg) 45%);
  color: var(--text);
}

.wrap {
  max-width: 760px;
  margin: 64px auto;
  padding: 0 20px;
}

h1 {
  font-size: clamp(2rem, 4vw, 3rem);
  margin: 0 0 8px;
}

p.sub {
  margin: 0 0 28px;
  color: var(--muted);
}

.links {
  display: grid;
  gap: 12px;
}

.links a {
  display: block;
  padding: 14px 16px;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 12px;
  color: var(--text);
  text-decoration: none;
  transition: transform .15s ease, border-color .15s ease;
}

.links a:hover {
  transform: translateY(-2px);
  border-color: var(--accent);
}
